import * as _ from "lodash";
import { Tile } from "./Tile";
import { GroupType, TileGroup } from "./TileGroup";

export class Board {
    public readonly size: number;
    public readonly length: number;

    private _tiles: Tile[][];
    private _locked = false;

    get isLocked(): boolean { return this._locked; }

    constructor(size: number) {
        this.size = size;
        this.length = size * size;
        this._tiles = [[]];

        const vGroups: TileGroup[] = [];
        const hGroups: TileGroup[] = [];
        const sGroups: TileGroup[] = [];
        for (let i: number = 0; i < this.length; i++) {
            vGroups[i] = new TileGroup(this, GroupType.Vertical);
            hGroups[i] = new TileGroup(this, GroupType.Horizontal);
            sGroups[i] = new TileGroup(this, GroupType.Square);
        }

        let vG: TileGroup;
        let hG: TileGroup;
        let sG: TileGroup;
        for (let x1: number = 0; x1 < size; x1++) {
            for (let y1: number = 0; y1 < size; y1++) {
                sG = sGroups[size * y1 + x1];
                for (let x2: number = 0; x2 < size; x2++) {
                    const x: number = size * x1 + x2;
                    if (!this._tiles[x]) {
                        this._tiles[x] = [];
                    }
                    vG = vGroups[x];
                    for (let y2: number = 0; y2 < size; y2++) {
                        const y: number = size * y1 + y2;
                        hG = hGroups[y];
                        this._tiles[x][y] = new Tile(vG, hG, sG);
                    }
                }
            }
        }
    }

    public clear(clearMarks: boolean = true): void {
        _.each(this._tiles, (row) => {
            _.each(row, (tile) => {
                if (!tile.isLocked) {
                    tile.value = undefined;
                }
                if (clearMarks) {
                    for (let i = 0; i < this.length; i++) {
                        tile.toggleNote(i, true);
                    }
                }
            });
        });
    }
    public getTile(x: number, y: number): Tile {
        this.validate(x);
        this.validate(y);
        return this._tiles[x][y];
    }
    public load(data: any): void {
        if (data[1] !== this.size) {
            throw Error("Bad data.");
        }
        _.each(data[2], (r, x: number) =>
            _.each(r, (d, y: number) =>
                this._tiles[x][y].data = d,
            ),
        );
        this._locked = data[0] === 1;
    }
    public lock(): void {
        if (this._locked) {
            throw Error("Values have already been locked in.");
        }
        for (const row of this._tiles) {
            for (const tile of row) {
                if (tile.value !== undefined) {
                    tile.lock();
                }
            }
        }
        this._locked = true;
    }
    public save() {
        return JSON.stringify([
            this._locked ? 1 : 0, this.size,
            _.map(this._tiles, (r) => _.map(r, (t) => t.data)),
        ]);
    }
    public validate(value: number): void {
        if (value < 0 || value >= this.length) {
            throw Error("Out of range.");
        }
        if (value != (value >> 0)) {
            throw Error("Not an integer.");
        }
    }
}



