import * as $ from "jquery";
import { Board } from "./Sudoku/index";
import { Tile } from "./Tile";

import "./style.scss";

const style = $("<style>").appendTo("head");
const div = $("<div id=\"board\" class=\"grid\">").prependTo("body");

function newBoard(size: number): Board {
    div.html("");
    style.html(`.grid{grid-template-columns:repeat(${size},1fr)}`);
    const board = new Board(size);

    for (let x1 = 0; x1 < size; x1++) {
        for (let y1 = 0; y1 < size; y1++) {
            const area = $("<div class=\"area grid\">");
            for (let x2 = 0; x2 < size; x2++) {
                const x = size * x1 + x2;
                for (let y2 = 0; y2 < size; y2++) {
                    const y = size * y1 + y2;
                    const tile = new Tile(board.getTile(x, y)).div;
                    area.append(tile);
                }
            }
            div.append(area);
        }
    }
    setTimeout(() => $(window).resize(), 200);
    return board;
}

const global = (window as any);
global.$ = $;
global.board = newBoard(3);
global.newBoard = (size: number) => {
   return global.board = newBoard(size);
};
global.save = () => `load(${global.board.save()})`;
global.load = (data: any) => {
    global.newBoard(data[1])
            .load(data);
};
$("body")
    .click(() => {
        Tile.higlighted = "";
        $(".tile").removeClass("highlight");
    })
    .dblclick(() => {
        global.board.lock();
    });



