import * as _ from "lodash";
import { Board } from "./Board";
import { Dispatcher } from "./Dispatcher";
import { GroupType, TileGroup } from "./TileGroup";

export enum Action { Set, Unset, Secondary }
export interface IUpdateArgs { action: Action; tile: Tile; value: number; }
export class Tile {
    public readonly board: Board;
    public readonly updates: Dispatcher<IUpdateArgs> = new Dispatcher<IUpdateArgs>();

    private _notify: (action: Action, value: number) => void;
    private _value: number = undefined;
    private _likely: boolean[] = [];
    private _locked: boolean = false;
    private _used: SymbolUsage[] = [];

    get isLocked(): boolean { return this._locked; }
    get value(): number { return this._value; }
    set value(value: number) {
        if (this._locked) {
            throw Error("You may not change this tile.");
        }
        if (value === null) {
            value = undefined;
        }
        if (value !== undefined) {
            this.board.validate(value);
            if (!this._used[value].isPossible) {
                throw Error("This value has already been eliminated.");
            }
        }
        if (this._value === value) {
            return;
        }
        const orig: number = this._value;
        this._value = value;
        if (orig !== undefined) {
            this._notify(Action.Unset, orig);
        }
        if (value !== undefined) {
            this._notify(Action.Set, value);
        }
    }
    get data(): any {
        return [
            this._locked ? 1 : 0, this._value,
            _.compact(
                _.map(this._likely, (b, i) =>
                    b ? 0 : i,
                ),
            ),
        ];
    }
    set data(data: any) {
        for (const i of data[2]) {
            this.toggleNote(i, false);
        }
        this.value = data[1];
        if (data[0] === 1) {
            this.lock();
        }
    }

    constructor(vGroup: TileGroup, hGroup: TileGroup, sGroup: TileGroup) {
        this.board = sGroup.board;
        if (hGroup.board !== this.board || vGroup.board !== this.board) {
            throw Error("All groups must belog to the same board.");
        }
        if (vGroup.type !== GroupType.Vertical ||
            vGroup.type !== GroupType.Vertical ||
            vGroup.type !== GroupType.Vertical) {
            throw Error("Please double-check your group types.");
        }
        for (let i: number = 0; i < this.board.length; i++) {
            this._likely[i] = true;
            this._used[i] = new SymbolUsage();
        }
        vGroup.add(this);
        hGroup.add(this);
        sGroup.add(this);
        this._notify = (action, value) => {
            vGroup.update(action, this, value);
            hGroup.update(action, this, value);
            sGroup.update(action, this, value);
            this.updates.dispatch({ action, tile: this, value });
        };
    }

    public lock(): void {
        if (this._value !== undefined) {
            this._locked = true;
            this.updates.dispatch({ action: Action.Secondary, tile: this, value: this._value });
        }
    }
    public note(value: number): "locked" | "set" | "possible" | "unlikely" | "" {
        if (this._value !== undefined) {
            if (this._value === value) {
                if (this._locked) {
                    return "locked";
                }
                return "set";
            }
            return "";
        }
        if (this._used[value].isPossible) {
            if (this._likely[value]) {
                return "possible";
            }
            return "unlikely";
        }
        return "";
    }
    public toggleNote(value: number, status?: boolean): void {
        this.board.validate(value);
        this._likely[value] = status === undefined ? !this._likely[value] : status;
        this.updates.dispatch({ action: Action.Secondary, tile: this, value });
    }
    public update(action: Action, tile: Tile, group: TileGroup, value: number): void {
        switch (action) {
            case Action.Set:
                this._used[value][group.type] = true;
                break;

            case Action.Unset:
                this._used[value][group.type] = false;
                break;
        }
        if (tile !== this) {
            this.updates.dispatch({ action: Action.Secondary, tile: this, value });
        }
    }
}
// tslint:disable-next-line:max-classes-per-file
class SymbolUsage {
    [index: number]: boolean;
    get isPossible(): boolean {
        return !(this[GroupType.Vertical] || this[GroupType.Horizontal] || this[GroupType.Square]);
    }
}



