export class Dispatcher<TArgs> {
    private _handlers: Array<(args: TArgs) => void> = [];

    public subscribe(handler: (args: TArgs) => void): void {
        this._handlers.push(handler);
    }
    public unsubscribe(handler: (args: TArgs) => void): void {
        const i: number = this._handlers.indexOf(handler);
        if (i > -1) {
            this._handlers.splice(i, 1);
        }
    }
    public dispatch(args: TArgs): void {
        for (const handler of this._handlers) {
            handler(args);
        }
    }
}



