import { Board } from "./Board";
import { Action, Tile } from "./Tile";

export enum GroupType { Vertical, Horizontal, Square }
export class TileGroup {
    public readonly board: Board;
    public readonly type: GroupType;

    private _list: Tile[] = [];

    constructor(board: Board, type: GroupType) {
        this.board = board;
        this.type = type;
    }

    public add(item: Tile): void {
        this._list[this._list.length] = item;
    }
    public update(action: Action, tile: Tile, value: number): void {
        for (const i of this._list) {
            i.update(action, tile, this, value);
        }
    }
}



